/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
* {
  &::-webkit-scrollbar {
    display: none;
  }
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.app1 {
  background-size: cover;
  background-image: url(../pexels-skylar-kang-6044820.jpg);
  height: calc(370vh + 10px);
  margin-top: calc(-70vh - 5px);
  font-family: MagicNight;
  background-position: center center;

  @font-face {
    font-family: MagicNight;
    src: url(./fonts/MAGICNIGHT.ttf);
  }

  .drink {
    height: 70vh;
    top: 15vh;
    width: min-content;
    position: sticky;
    margin-left: calc(95vw - 35vh);
  }

  /* width: 100vh; */
  .allList {
    .topList {
      background-image: url(https://slipknot1.com/wp-content/themes/adderall/assets/images/skadd.jpg);
      // background-image: url(https://www.moneytrans.eu/moneytransblog/wp-content/uploads/sites/40/2021/04/fiestas-moneytrans-blog.png);
      background-size: cover;
      background-position: center center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      height: calc(100vh + 5px);

      ul {
        margin-right: 10vw;
        z-index: 2;

        li {
          .drinkText {
            color: rgb(59, 111, 111);
            width: 80vw;
            font-family: MagicNight;
            max-width: 700px;
            font-size: 4vh;
            padding: 20px;
            background-color: #00000060;
            border-radius: 15px;
          }

          &:nth-child(1) {
            display: flex;
            justify-content: center;
            margin-top: 3rem;
            height: 300px;
          }

          .title {
            max-width: 700px;
            width: 90%;
            height: fit-content;
            z-index: 1;
            margin-bottom: 5rem;
            background-color: #00000060;
            padding: 10px;
            border-radius: 15px;
          }
        }
      }
    }

    .midList {
      display: flex;
      flex-direction: column;
      width: 40vw;
      margin-right: 40vw;
      margin-left: 20vw;
      height: 100vh;
      justify-content: center;
      padding-inline: 2vw;
      background-color: rgba(0, 0, 0, 0.159);

      p {
        // color: aliceblue;
        font-size: 18px;
        color: aquamarine;
      }

      li {
        // backdrop-filter: invert(80%);
        // font-family: sans-serif;
        font-size: 8vw;
        color: rgb(59, 111, 111);
        text-wrap: nowrap;
      }
    }

    .botList {
      display: flex;
      flex-direction: column;
      width: 40vw;
      margin-right: 40vw;
      margin-left: 20vw;
      height: 100vh;
      justify-content: center;
      padding-inline: 2vw;
      background-color: rgba(0, 0, 0, 0.159);

      p {
        // color: aliceblue;
        font-size: 18px;
        color: aquamarine;
      }

      li {
        // backdrop-filter: invert(80%);
        // font-family: sans-serif;
        font-size: 4vw;
        color: rgb(59, 111, 111);

        text-wrap: nowrap;
      }
    }
  }
}

